import { GatewayProvider } from './../../providers/gateway/gateway';
import { FeedPessoalProvider } from './../../providers/feed-pessoal/feed-pessoal';
import { LoadingController } from 'ionic-angular/components/loading/loading-controller';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Events } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-editar-categoria-pessoal',
  templateUrl: 'editar-categoria-pessoal.html',
  providers: [ FeedPessoalProvider ]
})
export class EditarCategoriaPessoalPage {
  
  /**
   * Feed pessoal
   */
  public feedPessoal = [];

  /**
   * Veículos
   */
  public gatewaysId = [];

  /**
   * Checado
   */
  public checked = false;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public loadingCtrl: LoadingController,
              public viewCtrl: ViewController,
              public events: Events,
              public feedPessoalProvider: FeedPessoalProvider,
              public gatewayProvider: GatewayProvider) {
  }

  /**
   * Busca o feed pessoal
   * 
   */
  ionViewDidEnter(){
   
    this.feedPessoalProvider.get()
    .then( feed => {

      // Verifica se tem o feed
      if( feed.length > 0 ) {
        
        // Seta a propriedade para abrir
        feed = feed.map( item => {
          item.open = false;
          return item;
        })
      
        // Seta os feeds
        this.feedPessoal = feed;
        console.log(this.feedPessoal);
        
      } else {
        this.feedPessoal = [];
      }
    })
    .catch( err => console.log( err ) );
  }

  /**
   * Abre e fecha as categorias do menu
   * @param index 
   * @param status 
   */
  openCategory( item ) {
    if( this.feedPessoal ) 
      item.open = !item.open;
  }

  /**
   * Volta para a pagina anterior
   * 
   */
  back() {
    this.viewCtrl.dismiss();
  }

  /**
   * Pega os gateways
   * 
   * @param gateway 
   */
  push( gateway ) {

    // Verifica se foi selecionado
    if( gateway.checked ) {

      // guarda o veiculo
      this.gatewaysId.push( gateway.id );

    } else this.gatewaysId.splice( this.gatewaysId.indexOf( gateway.id ) , 1);    
  }

  /**
   * Remove veiculos do feed pessoal
   *
   */
  remove() {
    // Mostra o loading
    const loading = this.loadingCtrl.create( { content: 'Retirando inscrição...' } );
    loading.present();

    // Faz a chamada
    this.gatewayProvider.unfollowMany( this.gatewaysId )
    .then( success => {
      this.events.publish( 'atualizar' );
      this.back();
    })
    .catch( err => console.log( err ) )
    .then( () => loading.dismiss() );
  }
}

